module.exports = "## Medios de Pago\n\n\n### Listado de Métodos de Pago\n\n> Esta API es Pública y no requiere del uso de API Key aunque debe generarse la conexión con la cuenta utilizando el botón de \"Utilizar Widget / API\" en la consola de Mobbex\n\nURL: https://mobbex.com/p/sources/list{codigo}?total={total}\n\n__Tipo:__  \nGET\n\n__Parametros:__\n\n_Obligatorios:_  \n- ```codigo```: Código de acceso generado por la consola de Mobbex\n- ```total```: Monto de la operación.\n\n```hint\nEjemplos de Total:\n- 100\n- 100.10\n- 100.00\n```\n\n__Ejemplo:__\n\n```code\nhttps://mobbex.com/p/sources/list/e463a332-242f-4f49-9962-714acb41f028?total=12500\n```\n\n- ```Código```: e463a332-242f-4f49-9962-714acb41f028\n- ```Total```: 12500\n\n__Respuesta__\n\n- ```result```: Resultado de la operación\n- ```data```: Array de datos de la operación.\n\n#### Data\n- ```source```: Posee los datos del medio de pago: nombre y referencia\n- ```view```: Posee el tipo de Medio de Pago\n- ```installments```: Nodo Opcional y sólo disponible en métodos de pago que poseen Cuotas. Array de Cuotas. Si el método posee cuotas encontrará un nodo \"list\"\n\n#### Installments -> List\n- ```count```: Cantidad real de Cuotas, es el divisor del monto total\n- ```name```: Nombre del sistema de cuotas\n- ```description```: Descripción opcional del sistema de cuotas.\n- ```totals```: Nodo donde se indican los recargos, descuentos y cuotas\n\n```code\nlang: json\n---\n{\n    \"totals\": {\n        \"currency\": {\n            \"value\": \"ARS\",\n            \"label\": \"Peso Argentino\",\n            \"symbol\": \"$\",\n            \"hidden\": false\n        },\n        \"installment\": {\n            \"amount\": 12500,\n            \"count\": 1\n        },\n        \"total\": 12500,\n        \"financial\": {\n            \"percentage\": 0,\n            \"amount\": 0\n        }\n    }\n}\n```\n\n\n### Imágenes de Métodos de Pago\n\nLos métodos de pago posee un nodo llamado \"reference\" que identifica los métodos de pago en Mobbex. Para obtener las imágenes se debe utilizar este nodo.\n\nURL: https://res.mobbex.com/images/sources/{reference}.{format}\n\n__Tipo:__  \nGET\n\n__Parametros:__\n\n_Obligatorios:_  \n- ```reference```: Referencia del método de pago obtenida desde la API del Listado\n- ```format```: El formato de la imagen. Sólo admite png o jpg\n\n__Ejemplo:__\n\n```code\nhttps://res.mobbex.com/images/sources/visa.frances.png\n```"

